* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik Distressed, cursive;
}

body {
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

a {
  position: fixed;
  top: 15px;
  left: 20px;
}

button[data-start], button[data-stop] {
  color: #fff;
  width: 500px;
  height: 148px;
  cursor: pointer;
  background: #70050a;
  border: none;
  border-radius: 10px;
  margin: 50px;
  font-size: 72px;
  position: relative;
}

button[data-start]:hover, button[data-stop]:hover {
  background: #920a14;
}

